import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
//import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Movies from '../components/Movies'; 
import Jokes from '../components/Jokes'; 
import { Link } from 'react-router-dom';
// Base page for Fun apps
const useStyles = makeStyles({
   root: {
    flexGrow: 1,
  },
  card: {
    maxWidth: 180,
    marginLeft: 5,
    marginTop:60,
    padding:5,
    boxShadow:`5px 10px #9E9E9E`

  },
   paper: {
    height: 240,
   width: 150,
  },
  control: {
//padding: spacing(2),
  },
});

const FunPage = () => {

/* function routeChange() {
    let path = `newPath`;
    this.props.history.push(path);
  }*/

const classes = useStyles();
// Place a card component for each app.
return (
  <>  
     <Grid container className={classes.root} spacing={6}>
      <Grid item xs={12}>

           <Grid container justify="center" spacing={8}>
            <Grid  item>
                   <Link to={'/funApps/Movies'} >
              <Card  className={classes.card}>
       <CardActionArea>
        <CardMedia
          component="img"
          alt= "Fun Movie Game"
          height="200"
          image='https://media.giphy.com/media/63I6FXZTXks2A/giphy.gif'
          title="Movie Game"
        />
         <h4 style= {{marginLeft: '35px'}}> Movie Mappo </h4>
      </CardActionArea>      
    </Card>    
    </Link>
            </Grid>
              
    <Grid  item>
    <Link to={'/funApps/jokes'} >
       <Card  className={classes.card}>
       <CardActionArea>
        <CardMedia
          component="img"
          alt= "Jokesss"
          height="200"
          image='https://media.giphy.com/media/OocLVILtd7ybm/giphy.gif'
          title="Jokes"
        />
        <h4 style= {{marginLeft: '35px'}}> Jokeeee </h4>
      </CardActionArea>      
    </Card>    
    </Link>
</Grid>

        </Grid>
</Grid>
    </Grid>
  </>
)
}

export default FunPage;