import React from 'react';
import PropTypes from 'prop-types';
//import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';


 const Gif = ({ gif, isFlippable, activeClas }) =>  {
    return (
   <> 	
    { !isFlippable ? (
    	<img src={gif} alt="gif" /> ) : (
     <span>
  	{ activeClas ? <img className="flip-card-front" src='https://media.giphy.com/media/CjmvTCZf2U3p09Cn0h/giphy.gif' alt="gif" /> : <img className="flip-card-back" src={gif}  alt="gif" />
    	}
</span>
    	)}
   </>
   )
}
Gif.propTypes = {
  gif: PropTypes.string.isRequired,
};

export default Gif;