import React from 'react';

const TalkJsPage = () => {
const codeSnip = () => { 	
           return (   
        <pre>{`
         var test = function(fn) {
   		    fn(); // call back function
    	   };
    	   var callbackFn = funtion(){
    	   	console.log("hello");
    	   }

    	   test(callbackFn)
          `}</pre>
		
			)
		}

const codeMap = () => {

	return(
    <pre>{`
      function map(arry, fn) {
      	var newArr = [];
      	for(var i = 0; i < arry.length; i++) {
      		newArr.push(fn(arry[i]));
      	}
      	return newArr;
      }

      var arr = [1, 2, 3];
      var multiplyArr = map(arr, function(j){
      	  return j * 2;
      })
      console.log(multiplyArr); // [2, 4, 6]

      var compareArr = map(arr, function(item){
      	  return item > 2;
      })

      console.log(compareArr) // [false, false true]
	`}</pre>
	)
}		
return (
    <div>
    <p>
        Flexibility that Javascript offers is one of the reasons which encourages different competitive client side
        technologies such as Angular, React, Vuejs. 

        Alright.... Lets grab a JS feature and walk through it and see if JS lives up to its hype. Lets take up callback feature in JS to understand how Javascript is quite powerful.
        <h5> Ok.. GO on .. tell me more about it. </h5>
        Fine then... Lets begin !!

        {codeSnip()}
        So , not only can I pass the callbackFn as an argument but I can execute it inside another funtion. 
        Pretty cool. This is quite a vibrant feature if used judiciously. You could end be re-using lot of code.

        Lets try to implement our a basic mapper function now to further appreciate the above cool feature.

        {codeMap()}

        If you observe above a single map function caters to several possibilities. Hence Javascript 
        indeed comes with such powerful features , again if used cautiously.
    </p>


    </div>
)};

export default TalkJsPage;