import React, { useState, useEffect } from 'react';
import { sortableContainer, sortableElement } from 'react-sortable-hoc';
import  { arrayMove } from 'react-sortable-hoc';
//import Grid from '@material-ui/core/Grid';
import './Movies.css';
import SortableGif from './SortableImg'
//import { makeStyles } from '@material-ui/core/styles';
import Gif from './Gif';

/*  const toggleClass = () => {
     setActiveClas(!activeClas);
     console.log("in toggle class");
  }*/
let winStatusNew = null;
const SortableGifsContainer = sortableContainer(({ children }) => <div className="flip-card-inner">{children}</div>);
//const SortableGif = sortableElement(({ gif }) => <Gif  key={gif} gif={gif} />);
/*   const useStyles = makeStyles({
   root: {
    flexGrow: 1,
  },
})*/

const initMovieArray = (moviesBaseArr) => {
  let newArr= [];
  //let j=0;
 //while (newArr.length != 4) {
   for(let j=0;j<4;j++) {
    let randomItem = moviesBaseArr[Math.floor(Math.random() * moviesBaseArr.length)];
    if(newArr.indexOf(randomItem) == -1) {
      newArr[j] = randomItem;       
   }
   else{
     j--;
     //continue;
   }
 }
   return newArr;
   
} 

const Movies = () => {
  const moviesBaseArray = ['tt0162222','tt0096895', 'tt0468569', 'tt0499549', 'tt0111161', 'tt0075148', 'tt0091121', 'tt0133093', 'tt0110357','tt2911666', 'tt0352248','tt0120812'];
 // const movieIdsArray = ['tt0162222','tt0096895', 'tt0468569', 'tt0499549'];
 const [movieIdsArray, setArry1] = useState(initMovieArray(moviesBaseArray));
const [movieIdsArray2, setArry2] = useState(initMovieArray(moviesBaseArray));
  let  moviePosterArray =[];
  let  moviePosterArray2 =[];
 // let movieDataArray = [];
  //let movieDataArray2 = [];
  const [count, setCount] = useState(0);
  const [gifs, setGifs] = useState([]);
  const [newGifs, setNewGifs] = useState([]);
  const [activeClas, setActiveClas] = useState(true);
  const [isFlippable, setFlippable] = useState(false);
  const [winStatus, setWinStatus] = useState('2');
  
  const [buttonMsg, setButtonMsg] =useState("Click When Ready");
  const [displayMsg, setDisplayMsg] = useState("");

   const setMovieInfo = (movieData,  type) => {
     //console.log("movie data");
     //console.log(movieData);
     
     //moviePosterArr.push(movieData.Poster);
     if(type === "primary"){
       moviePosterArray.push(movieData.Poster);
       setGifs([...moviePosterArray]);
     } else if( type === "secondary"){
       moviePosterArray2.push(movieData.Poster);
       setNewGifs([...moviePosterArray2]);
   }
     //movieDataArray.push(movieData);

    }

   const displayWinStatus = () => {
      let msg = null;
      console.log("win status new in disp " +winStatus);
       if (winStatusNew != null){
         if(winStatusNew) {
          setDisplayMsg(" You  Win ");
          }
         else {
           setDisplayMsg("Ah !! You  loose !! ");
         } 
       }
       //return msg;
    }


    const randomizeMovieArr = () => {
    // let randomArr = initMovieArray(moviesBaseArray);
    let randomArr = [...movieIdsArray];
     let tempArry = [...movieIdsArray2];
    //  randomArr.map((item) => {
      for(let k=0; k<randomArr.length; k++) {
          if (tempArry.indexOf(randomArr[k]) === -1){
            
            setWinStatus('0');
            winStatusNew = false;
            console.log("where winstatus is false" +winStatus);
            break;
          }
        }
      //})
      console.log("winStatus is "+winStatus);
      if(winStatusNew == null){      
          setWinStatus('1');
          console.log("where winstatus is true" +winStatus);
          winStatusNew = true;
      }
      displayWinStatus();
    }


    const getDataDetails = (movieArr, type) => {
           let fetchData = async (i) => {
            const res = await fetch(`https://www.omdbapi.com/?i=${movieArr[i]}&apikey=8a3391ad`);
            const body = await res.json();
           
            setMovieInfo(body, type);

        }
        for(let j=0; j<4; j++){
           setCount(j + 1);
         fetchData(j);
        }
    }

    useEffect(() => {
        getDataDetails(movieIdsArray,  "primary");
        getDataDetails(movieIdsArray2,  "secondary"); 
        
    }, []);
   /*const onSortEnd = ({oldIndex, newIndex}) => setGifs(arrayMove(gifs, oldIndex, newIndex));*/

  const onSortEnd = ({ oldIndex, newIndex, collection }) => {
    switch (collection) {
      case 'gifs':
        setGifs(arrayMove(gifs, oldIndex, newIndex))
        break;
      case 'newGifs':
        setNewGifs(arrayMove(newGifs, oldIndex, newIndex))
        break;
      default:
        break;
    }
  }


const flipImages = () => {
     setActiveClas(!activeClas);
     console.log("buttonMsg" +buttonMsg );
     if (buttonMsg == "Click When Ready"){
      console.log("if main");
        setButtonMsg("Reset");
        randomizeMovieArr();
     }
     else {
      // setArry2([]);
       setArry2(initMovieArray(moviesBaseArray));
       getDataDetails(movieIdsArray2,  "secondary");
       winStatusNew = null;
       setTimeout(() =>{
          //console.log("Sdf");
          setButtonMsg('Click When Ready');
       },600);
       
       setDisplayMsg('');
       setWinStatus('2');       
       
     }
}
//const frontImg = 'flip-card-front';
//const backImg ='flip-card-back';

//const classes = useStyles();

  return (
        <div className= "Movies">

         {  count > 3 ? (  
         <div>   
      <h4>Rearrange the first row of movies and try your luck</h4>
  
      <SortableGifsContainer axis="x" onSortEnd={onSortEnd} onSortStart={(_, event) => event.preventDefault()}>
        {gifs.map((gif, i) => ( 
         <SortableGif 
            index={i}
            key={gif}
            gif={gif}
            isFlippable = {false}
            collection="gifs"
          />         
          )
        )} 
      </SortableGifsContainer>
      
      <h4>....</h4>
      <div className = "set2">
   { newGifs.map((gif, i) => <Gif index={i} key={gif} gif={gif} collection="newGifs" activeClas={activeClas} isFlippable = {true}/>)}
      <br/>
      <button onClick = {() => flipImages()} > {buttonMsg} </button>
      </div>
      
      </div>
      ) : <h3> loading </h3> }
         
            <p className="first"> { displayMsg }  </p> 
            
    </div>
  );
}

export default Movies;